<template>
  <div>
    <div class="header">常规菜谱管理</div>
    <div style="border: 1px solid rgb(0, 0, 0, 0.1);">
      <div class="sss">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选查询</span>
        </div>
      </div>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="标题:">
          <el-input v-model="formInline.phone" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="状态:">
          <el-select v-model="formInline.customerType" placeholder="选择状态">
            <el-option label="所有" value="all"></el-option>
            <el-option label="正常" value="renyangkehu"></el-option>
            <el-option label="已截止" value="qianzaikehu"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">开始筛选</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="aaa">
      <div class="sss">
        <span>常规菜谱管理</span>
        <el-button size="mini" @click="addproject">新增</el-button>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column prop="name" label="标题"></el-table-column>
          <el-table-column prop="name" label="封面">
            <template slot-scope="scope">
              <img src alt />
            </template>
          </el-table-column>
          <el-table-column prop="name" label="类别"></el-table-column>
          <el-table-column prop="name" label="内容">
            <img src alt />
            <video src></video>
          </el-table-column>
          <el-table-column prop="name" label="创建时间"></el-table-column>
          <el-table-column prop="name" label="操作" width="250">
            <template slot-scope="scope">
              <el-button size="mini" @click="looking(scope.row)">查看</el-button>
              <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-show="page.pageTotal>10">
          <el-pagination
            :current-page.sync="page.currentPage"
            :page-size="page.pnum"
            layout="total, prev, pager, next , jumper"
            :total="page.pageTotal"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增 -->
    <div v-if="isadd" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增项目</p>
          </el-form-item>
          <el-form-item label="标题" label-width="110px">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="封面" label-width="110px">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.head_portrait" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="类别" label-width="110px">
           <el-select v-model="formInline.customerType" placeholder="选择状态">
              <el-option label="炖菜" value="renyangkehu"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="内容" label-width="110px">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="创建时间" label-width="110px">
            <el-input v-model="form.id_card"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 查看 -->
    <div v-if="islooking" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-row class="texts" :gutter="10" v-for="(item,index) in datas" :key="index">
          <el-col :span="10" style="text-align: right;">
            <div class="grid-content bg-purple">{{item.name}}</div>
          </el-col>
          <el-col :span="14">
            <div class="grid-content bg-purple">{{item.value}}</div>
          </el-col>
        </el-row>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增项目</p>
          </el-form-item>
          <el-form-item label="标题" label-width="110px">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="封面" label-width="110px">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
              :file-list="file_list"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.head_portrait" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="类别" label-width="110px">
           <el-select v-model="formInline.customerType" placeholder="选择状态">
              <el-option label="炖菜" value="renyangkehu"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="内容" label-width="110px">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="创建时间" label-width="110px">
            <el-input v-model="form.id_card"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该项目?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      islooking: false,
      isdeleted: false,
      ismodify: false,
      isadd: false,
      dialogImageUrl: "",
      file_list: [],
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      tableData: [{ name: "ssss" }, { name: "ssss" }, { name: "ssss" }], // 表格数据
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      },
      datas: [],
    };
  },
  methods: {
    // 搜索
    search() {},
    // 添加显示
    addproject() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.islooking) {
        alert("添加关闭");
        this.islooking = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(Modifyform) {
      this.ismodify = true;
      this.form = {};
      console.log(Modifyform);
    },
    // 删除事件
    deleted() {
      this.isdeleted = true;
    },
    // 显示添加
    looking(Modifyform) {
      console.log(Modifyform);
      this.datas = [
        { name: "标题", value: Modifyform.name },
        { name: "封面", value: Modifyform.name },
        { name: "类别", value: Modifyform.name },
        { name: "内容", value: Modifyform.name },
        { name: "创建时间", value: Modifyform.name },
        { name: "状态", value: Modifyform.name },
      ];
      this.islooking = true;
    },

    // 确认添加
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
        console.log(this.form);
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.dialogImageUrl = file.url;
    },
    //请求接口部分
    getGoodsList() {}
  },
  created() {
    this.getGoodsList();
  }
};
</script>

<style>
.header {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.4);
}
.sss {
  border-bottom: 1px solid rgb(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 22px;
  background-color: rgba(243, 243, 243, 1);
}
.demo-form-inline {
  padding-left: 20px;
}
.aaa {
  margin: 30px 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
}
.list {
  margin: 10px 10px;
}
</style>