<template>
  <div>
    <div class="header sss">
      <span>视频监控管理</span>
      <el-button type="mini" @click="refresh">
        <i class="el-icon-refresh"></i>
        刷新
      </el-button>
    </div>
    <!-- 搜索 -->
    <div style="border: 1px solid rgb(0, 0, 0, 0.1);">
      <div class="sss">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选查询</span>
        </div>
      </div>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item>
          <el-input v-model="formInline.phone" placeholder="输入屠宰厂名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <div class="aaa">
      <div class="sss">
        <div>
          <span>视频列表</span>
        </div>
        <el-button type="mini" @click="addMonitor">新增监控</el-button>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column type="index" label="序号"></el-table-column>
          <el-table-column prop="monitor_name" label="监控名称"></el-table-column>
          <el-table-column prop="device_id" label="设备编号"></el-table-column>
          <el-table-column prop="channel_number" label="通道号"></el-table-column>
          <el-table-column label="观看地址">
            <template slot-scope="scope">
              <a :href="scope.row.viewing_address">{{scope.row.viewing_address}}</a>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="所属场地"></el-table-column>
          <el-table-column prop="Installation_position" label="安装位置"></el-table-column>
          <el-table-column prop="addtime" label="添加时间"></el-table-column>
          <el-table-column prop="name" label="操作">
            <template slot-scope="scope">
              <el-button type="mini" @click="modify(scope.$index,scope.row)">修改</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 分页 -->
    <div class="block" v-show="page.pageTotal>10">
      <el-pagination
        :current-page.sync="page.currentPage"
        :page-size="page.pnum"
        layout="total, prev, pager, next , jumper"
        :total="page.pageTotal"
      ></el-pagination>
    </div>
    <!-- 新增 -->
    <div v-if="isadd" class="bg">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增监控</p>
          </el-form-item>
          <el-form-item label="监控名称">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label="设备编号">
            <el-input v-model="form.device_id"></el-input>
          </el-form-item>
          <el-form-item label="通道号">
            <el-input v-model="form.channel_number"></el-input>
          </el-form-item>
          <el-form-item label="观看地址">
            <el-input v-model="form.viewing_address"></el-input>
          </el-form-item>
          <el-form-item label="所属场地">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item label="安装位置">
            <el-input v-model="form.Installation_position"></el-input>
          </el-form-item>
          <el-form-item label="添加时间">
            <el-input v-model="form.addtime"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 修改 -->
    <div v-if="ismodify" class="bg">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">修改监控</p>
          </el-form-item>
          <el-form-item label="监控名称">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label="设备编号">
            <el-input v-model="form.device_id"></el-input>
          </el-form-item>
          <el-form-item label="通道号">
            <el-input v-model="form.channel_number"></el-input>
          </el-form-item>
          <el-form-item label="观看地址">
            <el-input v-model="form.viewing_address"></el-input>
          </el-form-item>
          <el-form-item label="所属场地">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item label="安装位置">
            <el-input v-model="form.Installation_position"></el-input>
          </el-form-item>
          <el-form-item label="添加时间">
            <el-input v-model="form.addtime"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该监控?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isdeleted: false, //显示删除弹框
      ismodify: false, //显示修改弹框
      isadd: false, //显示新增弹框
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      tableData: [
        {
          monitor_name: "监控1号",
          device_id: "B60Y178P00",
          channel_number: "通道号",
          viewing_address: "观看地址",
          address: "崇德牧场",
          Installation_position: "崇德牧场",
          addtime: "2020-03-25"
        }
      ], // 表格数据
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      },
      form: {
        monitor_name: "",
        device_id: "",
        channel_number: "",
        viewing_address: "",
        address: "",
        Installation_position: "",
        addtime: ""
      }
    };
  },
  methods: {
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 刷新
    refresh() {
      // this.$forceUpdate()
    },
    // 删除
    deleted(index) {
      console.log(index);
      this.isdeleted = true;
    },
    // 搜索
    search() {},
    // 显示添加
    addMonitor() {
      this.isadd = true;
    },
    // 显示修改表单
    modify(index, Modifyform) {
      this.ismodify = true;
      this.form = Modifyform;
      console.log(Modifyform);
    },
    // 确认添加监控
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加监控弹框
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //请求接口部分
    getGoodsList() {}
  },
  created() {
    this.getGoodsList();
  }
};
</script>

<style>
a{
  color: #606266;
  text-decoration: none;
}
.header {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.4);
}
.sss {
  border-bottom: 1px solid rgb(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 22px;
  background-color: rgba(243, 243, 243, 1);
}
.demo-form-inline {
  padding-left: 20px;
}
.aaa {
  margin: 30px 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
}
.list {
  margin: 10px 10px;
}
.refresh {
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 5px;
}
.video_box {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -312px 0 0 -181px;
  width: 362px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  z-index: 100;
}
</style>