<template>
  <div>
    <div class="header">任务列表</div>
    <div class="aaa">
      <div class="sss">
        <span>任务列表</span>
        <div>
          <el-button type="mini" @click="addtask">新增</el-button>
        </div>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column prop="name" label="所属牧场"></el-table-column>
          <el-table-column prop="name" label="任务标题"></el-table-column>
          <el-table-column prop="name" label="任务详情"></el-table-column>
          <el-table-column prop="name" label="顺序"></el-table-column>
          <el-table-column prop="name" label="添加时间"></el-table-column>
          <el-table-column prop="name" label="操作">
            <template slot-scope="scope">
              <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-show="page.pageTotal>10">
          <el-pagination
            :current-page.sync="page.currentPage"
            :page-size="page.pnum"
            layout="total, prev, pager, next , jumper"
            :total="page.pageTotal"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增 -->
    <div v-if="isadd" class="bg ">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增牦牛</p>
          </el-form-item>
          <el-form-item label-width="120px" label="所属牧场">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="任务标题">
            <el-input v-model="form.geographical_position"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="任务详情">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="顺序">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="添加时间">
            <el-date-picker v-model="form.claimTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增牦牛</p>
          </el-form-item>
          <el-form-item label-width="120px" label="所属牧场">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="任务标题">
            <el-input v-model="form.geographical_position"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="任务详情">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="顺序">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="添加时间">
            <el-date-picker v-model="form.claimTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该任务?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isdeleted: false,
      ismodify: false,
      isadd: false,
      tableData: [
        {
          name: "ssss"
        }
      ],
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      }
    };
  },
  methods: {
    // 显示添加
    addtask() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },

    // 搜索
    search() {},
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(index, Modifyform) {
      this.ismodify = true;
      this.fileList = [{ url: Modifyform.coverUrl }];
      this.form = {
        slaughterhouse_name: Modifyform.ranch_name,
        geographical_position: Modifyform.geographical_position,
        introduce: Modifyform.introduce
      };
      console.log(Modifyform.slaughterhouse_name);
    },
    // 删除banner图事件
    deleted() {
      this.isdeleted = true;
    },
    // 确认添加
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.dialogImageUrl = file.url;
    },
    //请求接口部分
    getGoodsList() {}
  }
};
</script>

