<template>
  <div>
    <div class="header">对屠宰厂评论</div>
    <div style="border: 1px solid rgb(0, 0, 0, 0.1);">
      <div class="sss">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选查询</span>
        </div>
      </div>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="评论内容:">
          <el-input v-model="formInline.phone" placeholder="请输入评论内容"></el-input>
        </el-form-item>
        <el-form-item label="牦牛耳标号:">
