        </el-form-item>
        <el-form-item label="认领用户手机号:">
          <el-input v-model="formInline.phone" placeholder="请输入认领用户手机号"></el-input>
        </el-form-item>
        <el-form-item label="牧民名字:">
          <el-input v-model="formInline.phone" placeholder="请输入牧民名字"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">开始筛选</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="aaa">
      <div class="sss">
        <span>对屠宰厂评论</span>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column prop="name" label="客户手机号"></el-table-column>
          <el-table-column prop="name" label="客户姓名"></el-table-column>
          <el-table-column prop="name" label="牦牛名字"></el-table-column>
          <el-table-column prop="name" label="耳标号"></el-table-column>
          <el-table-column prop="name" label="屠宰厂"></el-table-column>
          <el-table-column prop="name" label="评分"></el-table-column>
          <el-table-column prop="name" label="评论内容"></el-table-column>
          <el-table-column prop="name" label="评论时间"></el-table-column>
          <el-table-column prop="name" label="操作" width="260">
            <template slot-scope="scope">
              <el-button size="mini" @click="looking(scope.row)">查看</el-button>
              <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-show="page.pageTotal>10">
          <el-pagination
            :current-page.sync="page.currentPage"
            :page-size="page.pnum"
            layout="total, prev, pager, next , jumper"
            :total="page.pageTotal"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 查看 -->
    <div v-if="islooking" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-row class="texts" :gutter="10" v-for="(item,index) in datas" :key="index">
          <el-col :span="10" style="text-align: right;">
            <div class="grid-content bg-purple">{{item.name}}</div>
          </el-col>
          <el-col :span="14">
            <div class="grid-content bg-purple">{{item.value}}</div>
          </el-col>
        </el-row>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg butcher">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 ;font-size:25px">编辑对屠宰厂的评论</p>
          </el-form-item>
          <el-form-item label-width="150px" label="客户手机号">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="客户姓名">
            <el-input v-model="form.geographical_position"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="牦牛名字">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="耳标号">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="屠宰厂">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="评分">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="评论内容">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="评论时间">
            <el-date-picker v-model="form.claimTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该评论?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      islooking: false,
      isdeleted: false,
      ismodify: false,
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      tableData: [{ name: "ssss" }, { name: "ssss" }, { name: "ssss" }], // 表格数据
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      },
      datas: []
    };
  },
  methods: {
    // 搜索
    search() {},
    // 关闭弹框
    close() {
      if (this.islooking) {
        alert("添加关闭");
        this.islooking = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(Modifyform) {
      this.ismodify = true;
      this.form = {
      };
      console.log(Modifyform);
    },
    // 删除事件
    deleted() {
      this.isdeleted = true;
    },
    // 显示添加
    looking(Modifyform) {
      console.log(Modifyform);
      this.datas = [
        { name: "客户手机号", value: Modifyform.name },
        { name: "客户姓名", value: Modifyform.name },
        { name: "牦牛名字", value: Modifyform.name },
        { name: "耳标号", value: Modifyform.name },
        { name: "屠宰厂", value: Modifyform.name },
        { name: "评分", value: Modifyform.name },
        { name: "评论内容", value: Modifyform.name },
        { name: "评论时间", value: Modifyform.name }
      ];
      this.islooking = true;
    },

    // 确认添加
    really() {
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加
    cancel() {
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //请求接口部分
    getGoodsList() {}
  },
  created() {
    this.getGoodsList();
  }
};
</script>

<style>
.header {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.4);
}
.sss {
  border-bottom: 1px solid rgb(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 22px;
  background-color: rgba(243, 243, 243, 1);
}
.demo-form-inline {
  padding-left: 20px;
}
.aaa {
  margin: 30px 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
}
.list {
  margin: 10px 10px;
}
</style>