<template>
  <div>
    <div class="header">宣传介绍管理</div>
    <div class="aaa">
      <div class="sss">
        <span>宣传介绍管理</span>
        <el-button size="mini" @click="addpropagat">新增</el-button>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column type="index" label="序号"></el-table-column>
          <el-table-column prop="name" label="封面"></el-table-column>
          <el-table-column prop="name" label="链接">
            <template slot-scope="scope">
              <a :href="scope.row.url">{{scope.row.name}}</a>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="创建时间"></el-table-column>
          <el-table-column prop="name" label="状态"></el-table-column>
          <el-table-column prop="name" label="操作">
            <template slot-scope="scope">
               <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-show="page.pageTotal>10">
          <el-pagination
            :current-page.sync="page.currentPage"
            :page-size="page.pnum"
            layout="total, prev, pager, next , jumper"
            :total="page.pageTotal"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增 -->
    <div v-if="isadd" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增帮扶</p>
          </el-form-item>
          <el-form-item label="封面" label-width="110px">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.head_portrait" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="链接" label-width="110px">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="创建时间" label-width="110px">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="状态" label-width="110px">
            <el-select v-model="formInline.customerType" placeholder="选择状态">
              <el-option label="正常" value="renyangkehu"></el-option>
              <el-option label="已截止" value="qianzaikehu"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg butcher">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
       <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增帮扶</p>
          </el-form-item>
          <el-form-item label="封面" label-width="110px">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
              :file-list="file_list"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.head_portrait" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="链接" label-width="110px">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="创建时间" label-width="110px">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="状态" label-width="110px">
            <el-select v-model="formInline.customerType" placeholder="选择状态">
              <el-option label="正常" value="renyangkehu"></el-option>
              <el-option label="已截止" value="qianzaikehu"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该评论?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isdeleted: false,
      ismodify: false,
      isadd: false,
      dialogImageUrl: "",
      file_list: [],
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      tableData: [{ name: "ssss" }, { name: "ssss" }, { name: "ssss" }], // 表格数据
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      }
    };
  },
  methods: {
    // 搜索
    search() {},
    // 添加显示
    addpropagat() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(Modifyform) {
      this.ismodify = true;
      this.form = {};
      console.log(Modifyform);
    },
    // 删除事件
    deleted() {
      this.isdeleted = true;
    },
    // 确认添加
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
        console.log(this.form);
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.dialogImageUrl = file.url;
    },
    //请求接口部分
    getGoodsList() {}
  }
};
</script>

