<script>
export default {
  data() {
    return {
      isdeleted: false,
      ismodify: false,
      isadd: false,
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      tableData: [
        {
          name: "aaa"
        }
      ], // 表格数据
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      },
      form: {
        name: "",
        head_img: "", //头像
        type: "", //客户类型
        wechat_name: "", //微信昵称
        tel: "", //手机号
        yaks: "旺财", //认养牦牛
        id_card: "", //身份证号码
        total_balance: "", //总余额
        frozen_balance: "" //冻结余额
      }
    };
  },
  methods: {
    // 分页-每页条数
    handleSizeChange(val) {
      this.page.pnum = val;
    },
    // 当前页
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    // 添加显示
    addAddress() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },
    // 删除事件
    deleted() {
      this.isdeleted = true;
    },
    // 搜索
    search() {},
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(index, Modifyform) {
      this.ismodify = true;

      this.head_imgs = [{ url: Modifyform.head_img }];
      this.form = {
        name: Modifyform.name,
        tel: Modifyform.tel,
        id_card: Modifyform.id_card, //身份证号码
        wechat_name: Modifyform.wechat_name, //介绍
        type: Modifyform.type, //所属屠宰厂
        yaks: Modifyform.yaks, //综合评分
        total_balance: Modifyform.total_balance, //总余额
        freezing_balance: Modifyform.freezing_balance //冻结余额
      };
      console.log(Modifyform);
    },
    // 删除banner图事件
    deleted() {
      this.isdeleted = true;
    },
    // 确认添加
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
        console.log(this.form);
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        this.isdeleted = false;
        alert("重置成功");
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消重置");
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.head_imgs = file.url;
    },
    //请求接口部分
    getGoodsList() {}
  },
  created() {
    this.getGoodsList();
  }
};
</script>

