<template>
  <div>
    <div class="header">客户列表</div>
    <div style="border: 1px solid rgb(0, 0, 0, 0.1);">
      <div class="sss">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选查询</span>
        </div>
      </div>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="用户手机号:">
          <el-input v-model="formInline.tel" placeholder="请输入用户手机号"></el-input>
        </el-form-item>
        <el-form-item label="微信昵称:">
          <el-input v-model="formInline.wechat_name" placeholder="请输入微信昵称"></el-input>
        </el-form-item>
        <el-form-item label="真实姓名:">
          <el-input v-model="formInline.real_name" placeholder="请输入真实姓名"></el-input>
        </el-form-item>
        <el-form-item label="类型">
          <el-select v-model="formInline.type" placeholder="选择类型">
            <el-option label="所有" value></el-option>
            <el-option label="认养客户" value="1"></el-option>
            <el-option label="潜在客户" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">开始筛选</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="aaa">
      <div class="sss">
        <span>客户列表</span>
        <div>
          <el-button type="mini" @click="addherdsman">新增</el-button>
        </div>
      </div>
      <div class="list">
        <el-table
          :data="tableData.slice((page.currentPage-1)*page.pnum,page.currentPage*page.pnum)"
          ref="table"
          border
          stripe
          style="width: 100%"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" label="编号"></el-table-column>
          <el-table-column width="100" label="头像">
            <template slot-scope="scope">
              <img :src="scope.row.head_img" alt />
            </template>
          </el-table-column>
          <el-table-column prop="wechat_name" label="微信昵称"></el-table-column>
          <el-table-column prop="tel" width="120" label="手机号码"></el-table-column>
          <el-table-column prop="type" label="类型"></el-table-column>
          <el-table-column prop="total_balance" width="140" label="账户总余额（元）"></el-table-column>
          <el-table-column prop="freezing_balance" width="120" label="冻结余额（元）"></el-table-column>
          <el-table-column prop="real_name" label="真实姓名"></el-table-column>
          <el-table-column prop="id_card" width="170" label="身份证号码"></el-table-column>
          <el-table-column prop="yaks" label="认养牦牛"></el-table-column>
          <el-table-column prop="creat_time" label="注册时间"></el-table-column>
          <el-table-column label="操作" width="170px">
            <template slot-scope="scope">
              <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="reset(scope.$index)">重置密码</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            :current-change="handleCurrentChange"
            :page-size="page.pnum"
            layout="total, prev, pager, next , jumper"
            :total="tableData.lenght"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增 -->
    <div v-if="isadd" class="bg butcher">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增客户</p>
          </el-form-item>
          <el-form-item label="姓名" label-width="110px">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="头像" label-width="110px">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.head_img" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="账号" label-width="110px">
            <el-input v-model="form.account"></el-input>
          </el-form-item>
          <el-form-item label="密码" label-width="110px">
            <el-input v-model="form.password"></el-input>
          </el-form-item>
          <el-form-item label="手机号码" label-width="110px">
            <el-input v-model="form.tel"></el-input>
          </el-form-item>
          <el-form-item label="身份证号码" label-width="110px">
            <el-input v-model="form.id_card"></el-input>
          </el-form-item>
          <el-form-item label="微信昵称" label-width="110px">
            <el-input v-model="form.wechat_name"></el-input>
          </el-form-item>
          <el-form-item label="客户类型" label-width="110px">
            <el-input v-model="form.type"></el-input>
          </el-form-item>
          <el-form-item label="认养牦牛" label-width="110px">
            <el-input v-model="form.yaks"></el-input>
          </el-form-item>
          <el-form-item label="总余额" label-width="110px">
            <el-input v-model="form.total_balance"></el-input>
          </el-form-item>
          <el-form-item label="冻结余额" label-width="110px">
            <el-input v-model="form.freezing_balance"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg butcher">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增客户</p>
          </el-form-item>
          <el-form-item label="姓名" label-width="110px">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="头像" label-width="110px">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
              :file-list="head_imgs"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.head_img" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="手机号码" label-width="110px">
            <el-input v-model="form.tel"></el-input>
          </el-form-item>
          <el-form-item label="身份证号码" label-width="110px">
            <el-input v-model="form.id_card"></el-input>
          </el-form-item>
          <el-form-item label="微信昵称" label-width="110px">
            <el-input v-model="form.wechat_name"></el-input>
          </el-form-item>
          <el-form-item label="客户类型" label-width="110px">
            <el-input v-model="form.type"></el-input>
          </el-form-item>
          <el-form-item label="认养牦牛" label-width="110px">
            <el-input v-model="form.yaks"></el-input>
          </el-form-item>
          <el-form-item label="总余额" label-width="110px">
            <el-input v-model="form.total_balance"></el-input>
          </el-form-item>
          <el-form-item label="冻结余额" label-width="110px">
            <el-input v-model="form.freezing_balance"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 重置密码 -->
    <div v-if="isreset" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">重置密码</p>
          </el-form-item>
          <el-form-item label="登陆密码">
            <el-input type="password" v-model="form.login_password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码">
            <el-input type="password" v-model="form.confirm_password"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getCustomerList,
  postCustomer,
  modifyCustomer
} from "../../router/api";
export default {
  data() {
    return {
      isreset: false,
      ismodify: false,
      isadd: false,
      dialogImageUrl: "",
      head_imgs: [],
      formInline: {
        tel: "",
        wechat_name: "",
        real_name: "",
        type: ""
      },
      tableData: [
        {
          name: "李四",
          head_img: require("../../image/u3036.png"), //头像
          tel: "18200000000",
          id_card: "510724199706232816", //身份证号码
          wechat_name: "xiaoli", //微信昵称
          type: "认养客户", //客户类型
          yaks: "旺财", //认养牦牛
          slaughterhouse: "小金崇德屠宰厂", //所属屠宰厂
          total_balance: "200", //总余额
          freezing_balance: "300", //冻结余额
          create_time: "" //创建时间
        },
        {
          name: "李四",
          head_img: require("../../image/u3036.png"), //头像
          tel: "18200000000",
          id_card: "510724199706232816", //身份证号码
          wechat_name: "xiaoli", //微信昵称
          type: "认养客户",
          yaks: "旺财", //认养牦牛
          slaughterhouse: "小金崇德屠宰厂", //所属屠宰厂
          total_balance: "200", //总余额
          freezing_balance: "300", //冻结余额
          create_time: "" //创建时间
        },
        {
          name: "李四",
          head_img: require("../../image/u3036.png"), //头像
          tel: "18200000000",
          id_card: "510724199706232816", //身份证号码
          wechat_name: "xiaoli", //微信昵称
          type: "认养客户",
          yaks: "旺财", //认养牦牛
          slaughterhouse: "小金崇德屠宰厂", //所属屠宰厂
          total_balance: "200", //总余额
          freezing_balance: "300", //冻结余额
          create_time: "" //创建时间
        },
        {
          name: "李四",
          head_img: require("../../image/u3036.png"), //头像
          tel: "18200000000",
          id_card: "510724199706232816", //身份证号码
          wechat_name: "xiaoli", //微信昵称
          type: "认养客户",
          yaks: "旺财", //认养牦牛
          total_balance: "200", //总余额
          freezing_balance: "300", //冻结余额
          create_time: "" //创建时间
        }
      ], // 表格数据
      form: {
        name: "",
        type: "", //客户类型
        wechat_name: "", //微信昵称
        account: "", //账号
        password: "", //密码
        tel: "", //手机号
        yaks: "旺财", //认养牦牛
        id_card: "", //身份证号码
        total_balance: "", //总余额
        frozen_balance: "" //冻结余额
      },
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        // pageTotal: 15,
        //当前页，从1开始
        currentPage: 1
      }
    };
  },

  methods: {
    // 分页-每页条数
    handleSizeChange(val) {
      this.page.pnum = val;
    },
    // 当前页
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    // 重置密码显示
    reset(index) {
      this.isreset = true;
    },
    // 添加显示
    addherdsman() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },

    // 搜索
    search() {
      let data = this.formInline;
      console.log(data);
      getCustomerList(data).then(res => {
        console.log(res);
      });
    },
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isreset) {
        alert("重置关闭");
        this.isreset = false;
      }
    },
    // 编辑
    modify(index, Modifyform) {
      this.ismodify = true;
      this.head_imgs = [{ url: Modifyform.head_img }];
      this.form = {
        name: Modifyform.name,
        tel: Modifyform.tel,
        id_card: Modifyform.id_card, //身份证号码
        wechat_name: Modifyform.wechat_name, //介绍
        type: Modifyform.type, //所属屠宰厂
        yaks: Modifyform.yaks, //综合评分
        total_balance: Modifyform.total_balance, //总余额
        freezing_balance: Modifyform.freezing_balance //冻结余额
      };
      console.log(Modifyform);
    },
    // 删除banner图事件
    deleted() {
      this.isdeleted = true;
    },
    // 确认添加
    really(data) {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
        let data = this.form;
        data.head_img = this.head_imgs[0];
        postCustomer(data).then(res => {
          console.log(res);
        });
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
        data.head_img = this.head_imgs[0];
        modifyCustomer(data).then(res => {
          console.log(res);
        });
      }
      if (this.isreset) {
        this.isreset = false;
        alert("重置成功");
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isreset) {
        alert("已取消重置");
        this.isreset = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.head_imgs = file.url;
      this.head_imgs = this.head_imgs.slice(5);
      console.log(file.url);
    },
    //请求接口部分
    getGoodsList() {
      // console.log("aaa")
      getCustomerList().then(res => {
        console.log(res);
      });
    }
  },
  created() {
    this.getGoodsList();
  }
};
</script>

<style>
.header {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.4);
}
.sss {
  border-bottom: 1px solid rgb(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 22px;
  background-color: rgba(243, 243, 243, 1);
}
.demo-form-inline {
  padding-left: 20px;
}
.aaa {
  margin: 30px 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
}
.list {
  margin: 10px 10px;
}
</style>