<template>
  <div>
    <div class="header sss">
      <span>屠宰厂管理</span>
      <el-button type="mini" @click="refresh">
        <i class="el-icon-refresh"></i>
        刷新
      </el-button>
    </div>
    <div style="border: 1px solid rgb(0, 0, 0, 0.1);">
      <div class="sss">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选查询</span>
        </div>
      </div>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item>
          <el-input v-model="formInline.phone" placeholder="输入屠宰厂名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="aaa">
      <div class="sss">
        <div>
          <i class="el-icon-s-unfold"></i>
          <span>屠宰厂列表</span>
        </div>
        <el-button type="mini" @click="addSlaughterhouse">新增</el-button>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column prop="slaughterhouse_name" label="屠宰厂名称"></el-table-column>
          <el-table-column label="封面">
            <template slot-scope="scope">
              <img :src="scope.row.coverUrl" alt />
            </template>
          </el-table-column>
          <el-table-column prop="geographical_position" label="地理位置"></el-table-column>
          <el-table-column prop="introduce" label="介绍"></el-table-column>
          <el-table-column prop="name" label="操作">
            <template slot-scope="scope">
              <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="block" v-show="page.pageTotal>10">
      <el-pagination
        :current-page.sync="page.currentPage"
        :page-size="page.pnum"
        layout="total, prev, pager, next , jumper"
        :total="page.pageTotal"
      ></el-pagination>
    </div>
    <!-- 新增 -->
    <div v-if="isadd" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增屠宰厂</p>
          </el-form-item>
          <el-form-item label="屠宰厂名">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label="封面">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.coverUrl" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="地理位置">
            <el-input v-model="form.channel_number"></el-input>
          </el-form-item>
          <el-form-item label="介绍">
            <el-input v-model="form.viewing_address"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg">
      <div class="video_box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">编辑屠宰厂</p>
          </el-form-item>
          <el-form-item label="屠宰厂名">
            <el-input v-model="form.slaughterhouse_name"></el-input>
          </el-form-item>
          <el-form-item label="封面">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
              :file-list="fileList"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.coverUrl" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="地理位置">
            <el-input v-model="form.geographical_position"></el-input>
          </el-form-item>
          <el-form-item label="介绍">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该监控?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isdeleted: false,
      ismodify: false,
      isadd: false,
      dialogImageUrl: "",
      fileList: [],
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      // 表格数据
      tableData: [
        {
          slaughterhouse_name: "小金崇德屠宰厂",
          coverUrl: require("../../image/u3036.png"),
          geographical_position: "小金崇德屠宰厂",
          introduce: "啊是多久啊是扩大"
        }
      ],
      // 表单
      form: {
        slaughterhouse_name: "",
        coverUrl: "",
        geographical_position: "",
        introduce: ""
      },
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 100,
        //当前页，从1开始
        currentPage: 1
      }
    };
  },
  methods: {
    // 显示添加
    addSlaughterhouse() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },
    // 刷新
    refresh() {
      // this.$forceUpdate()
    },
    // 搜索
    search() {},
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(index, Modifyform) {
      this.ismodify = true;
      this.fileList = [{ url: Modifyform.coverUrl }];
      this.form = {
        slaughterhouse_name: Modifyform.slaughterhouse_name,
        geographical_position: Modifyform.geographical_position,
        introduce: Modifyform.introduce
      };
      console.log(Modifyform.slaughterhouse_name);
    },
    // 删除banner图事件
    deleted() {
      this.isdeleted = true;
    },
    // 确认添加
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.dialogImageUrl = file.url;
    },
    //请求接口部分
    getGoodsList() {}
  },
  created() {
    this.getGoodsList();
  }
};
</script>

<style>
.block {
  text-align: center;
}
.header {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.4);
}
.sss {
  border-bottom: 1px solid rgb(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 22px;
  background-color: rgba(243, 243, 243, 1);
}
.demo-form-inline {
  padding-left: 20px;
}
.aaa {
  margin: 30px 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
}
.list {
  margin: 10px 10px;
}
.refresh {
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 5px;
}
</style>