    search() {},
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isreset) {
        alert("重置关闭");
        this.isreset = false;
      }
    },
