<template>
  <div>
    <div class="header">banner管理</div>
    <div class="aaa">
      <div class="sss">
        <span>轮播图管理</span>
        <el-button size="mini" @click="addBnner">新增</el-button>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column type="index" label="序号"></el-table-column>
          <el-table-column prop="picture" label="图片">
            <template slot-scope="scope">
              <img :src="scope.row.pictureUrl" alt />
            </template>
          </el-table-column>
          <el-table-column prop="address" label="跳转地址"></el-table-column>
          <el-table-column label="设为首页">
            <template slot-scope="scope">
              <el-switch v-model="scope.row.isHome" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" @click="deleted">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p >确定要删除该图片?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
    <!-- 添加 -->
    <div v-if="isadd" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增图片</p>
          </el-form-item>
          <el-form-item label="上传图片">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="dialogImageUrl" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label="跳转地址">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item label="设为首页">
            <el-switch v-model="form.isHome" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogImageUrl: "", //上传图片的地址
      isadd: false, //显示添加弹框
      isdeleted: false, //显示删除弹框
      //表格数据
      tableData: [
        {
          pictureUrl: require("../../image/u3036.png"),
          address: "",
          isHome: true
        },
        {
          pictureUrl: require("../../image/u3036.png"),
          address: "",
          isHome: true
        }
      ],
      //添加弹框的表单内容
      form: {
        pictureUrl: "",
        address: "",
        isHome: false
      }
    };
  },
  methods: {
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("新增关闭");
        this.isadd = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 删除banner图事件
    deleted() {
      this.isdeleted = true;
    },
    //显示添加弹框
    addBnner() {
      this.isadd = true;
    },
    // 确定添加banner图
    really() {
      if (this.isadd) {
        this.form.pictureUrl = this.dialogImageUrl;
        console.log(this.form);
        this.isadd = false;
      }
      if (this.isdeleted) {
        console.log("已删除");
        this.isdeleted = false;
      }
    },
    //取消添加banner隐藏弹框
    cancel() {
      if (this.isadd) {
        this.isadd = false;
      }
      if (this.isdeleted) {
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      console.log(file)
      this.dialogImageUrl = file.url;
    }
  }
};
</script>

<style>
.bg {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0, 0.2);
  z-index: 10;
}
.box {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -181px 0 0 -181px;
  width: 362px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  z-index: 100;
}
.shangchan .el-upload--picture-card {
  width: 50px;
  height: 50px;
  line-height: 50px;
}
.shangchan .el-upload-list--picture-card .el-upload-list__item {
  width: 50px;
  height: 50px;
}
.wz {
  cursor:pointer;
  font-size: 26px;
  height: 25px;
  width: 25x;
  position: absolute;
  right: 15px;
  top: 10px;
  z-index: 1000;
}
.del {
  text-align: center;
  line-height: 60px;
  padding: 0 30px ;
}
</style>