        <el-form-item label="性别">
          <el-select v-model="formInline.customerType" placeholder="选择性别">
            <el-option label="所有" value="all"></el-option>
            <el-option label="公" value="renyangkehu"></el-option>
            <el-option label="母" value="qianzaikehu"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="认领用户手机号:">
          <el-input v-model="formInline.realName" placeholder="认领用户手机号"></el-input>
        </el-form-item>
        <el-form-item label="专属牧民:">
          <el-input v-model="formInline.realName" placeholder="专属牧民"></el-input>
        </el-form-item>
        <el-form-item label="牦牛状态">
          <el-select v-model="formInline.customerType" placeholder="选择状态">
            <el-option label="所有" value="all"></el-option>
            <el-option label="生长中" value="renyangkehu"></el-option>
            <el-option label="待屠宰" value="qianzaikehu"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="search">开始筛选</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="aaa">
      <div class="sss">
        <span>牦牛列表</span>
        <div>
          <el-button type="mini" @click="addyakList">新增</el-button>
        </div>
      </div>
      <div class="list">
        <el-table :data="tableData" ref="table" border stripe style="width: 100%">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="name" label="牦牛名字"></el-table-column>
          <el-table-column prop="name" label="图片">
            <template slot-scope="scope">
              <img src alt />
            </template>
          </el-table-column>
          <el-table-column prop="name" label="耳标号"></el-table-column>
          <el-table-column prop="name" label="出生日期"></el-table-column>
          <el-table-column prop="name" label="性别"></el-table-column>
          <el-table-column prop="name" label="所在牧场"></el-table-column>
          <el-table-column prop="name" width="120" label="认领用户手机号"></el-table-column>
          <el-table-column prop="name" label="认领时间"></el-table-column>
          <el-table-column prop="name" width="110" label="指定专属牧民"></el-table-column>
          <el-table-column prop="name" width="150" label="指定专属牧民手机号"></el-table-column>
          <el-table-column prop="name" label="牦牛状态"></el-table-column>
          <el-table-column prop="name" label="备注"></el-table-column>
          <el-table-column prop="name" width="145" label="操作">
            <template slot-scope="scope">
              <el-button type="mini" @click="modify(scope.$index,scope.row)">编辑</el-button>
              <el-button type="mini" @click="deleted(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-show="page.pageTotal>10">
          <el-pagination
            :current-page.sync="page.currentPage"
            :page-size="page.pnum"
            layout="total, prev, pager, next , jumper"
            :total="page.pageTotal"
          ></el-pagination>
        </div>
      </div>
    </div>

    <!-- 新增 -->
    <div v-if="isadd" class="bg butcher">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增牦牛</p>
          </el-form-item>
          <el-form-item label-width="120px" label="牦牛名字">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="图片">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.coverUrl" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label-width="120px" label="耳标号">
            <el-input v-model="form.geographical_position"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="出生日期">
            <el-date-picker v-model="form.date" type="date" placeholder="选择日期"></el-date-picker>
          </el-form-item>
          <el-form-item label-width="120px" label="所在牧场">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="认领用户手机号">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="认领时间">
            <el-date-picker v-model="form.claimTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
          <el-form-item label-width="120px" label="指定专属牧民">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="牦牛状态">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="备注">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 编辑 -->
    <div v-if="ismodify" class="bg butcher">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <el-form label-width="80px" :model="form">
          <el-form-item>
            <p style="margin:10px 0 10px 50px;font-size:25px">新增牦牛</p>
          </el-form-item>
          <el-form-item label-width="120px" label="牦牛名字">
            <el-input v-model="form.monitor_name"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="图片">
            <el-upload
              class="shangchan"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-success="handlePictureCardPreview"
              :on-remove="handleRemove"
              :file-list="fileList"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog>
              <img width="40px" :src="form.coverUrl" alt />
            </el-dialog>
          </el-form-item>
          <el-form-item label-width="120px" label="耳标号">
            <el-input v-model="form.geographical_position"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="出生日期">
            <el-date-picker v-model="form.date" type="date" placeholder="选择日期"></el-date-picker>
          </el-form-item>
          <el-form-item label-width="120px" label="所在牧场">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="认领用户手机号">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="认领时间">
            <el-date-picker v-model="form.claimTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
          </el-form-item>
          <el-form-item label-width="120px" label="指定专属牧民">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="牦牛状态">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item label-width="120px" label="备注">
            <el-input v-model="form.introduce"></el-input>
          </el-form-item>
          <el-form-item>
            <template slot-scope="scope">
              <el-button @click="cancel">取消</el-button>
              <el-button @click="really">确定</el-button>
            </template>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- 删除 -->
    <div v-if="isdeleted" class="bg">
      <div class="box">
        <div>
          <i class="el-icon-close wz" @click="close"></i>
        </div>
        <div class="del">
          <p>确定要删除该监控?</p>
          <el-button @click="cancel">取消</el-button>
          <el-button @click="really">确定</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isdeleted: false,
      ismodify: false,
      isadd: false,
      dialogImageUrl: "",
      fileList: [],
      formInline: {
        phone: "",
        wxNickName: "",
        realName: "",
        customerType: ""
      },
      tableData: [{ name: "cccc" }], // 表格数据
      page: {
        //每页数据量
        pnum: 10,
        //数据总数
        pageTotal: 0,
        //当前页，从1开始
        currentPage: 1
      }
    };
  },
  methods: {
    // 显示添加
    addyakList() {
      this.form = {};
      console.log("ss");
      this.isadd = true;
    },

    // 搜索
    search() {},
    // 关闭弹框
    close() {
      if (this.isadd) {
        alert("添加关闭");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改关闭");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除关闭");
        this.isdeleted = false;
      }
    },
    // 编辑
    modify(index, Modifyform) {
      this.ismodify = true;
      this.fileList = [{ url: Modifyform.coverUrl }];
      this.form = {
        slaughterhouse_name: Modifyform.ranch_name,
        geographical_position: Modifyform.geographical_position,
        introduce: Modifyform.introduce
      };
      console.log(Modifyform.slaughterhouse_name);
    },
    // 删除banner图事件
    deleted() {
      this.isdeleted = true;
    },
    // 确认添加
    really() {
      if (this.isadd) {
        alert("添加成功");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("修改成功");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("删除成功");
        this.isdeleted = false;
      }
    },
    //隐藏添加
    cancel() {
      if (this.isadd) {
        alert("已取消添加");
        this.isadd = false;
      }
      if (this.ismodify) {
        alert("已取消修改");
        this.ismodify = false;
      }
      if (this.isdeleted) {
        alert("已取消删除");
        this.isdeleted = false;
      }
    },
    //移除上传事件
    handleRemove(file, fileList) {},
    //上传成功事件
    handlePictureCardPreview(esponse, file, fileList) {
      this.dialogImageUrl = file.url;
    },
    //请求接口部分
    getGoodsList() {}
  },
  created() {
    this.getGoodsList();
  }
};
</script>

<style>
.header {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(0, 0, 0, 0.4);
}
.sss {
  border-bottom: 1px solid rgb(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 22px;
  background-color: rgba(243, 243, 243, 1);
}
.demo-form-inline {
  padding-left: 20px;
}
.aaa {
  margin: 30px 5px;
  border: 1px solid rgb(0, 0, 0, 0.1);
}
.list {
  margin: 10px 10px;
}
</style>