<script>
import Navigation from "../components/Navigation";
export default {
  components: {
    Navigation
  },
  data() {
    return {
      open: require("../image/open2.png"),
      personnel: require("../image/open2.png"),
      colors: "#cccccc",
      color1: "#cccccc"
    };
  },
  methods: {
    over() {
      this.open = require("../image/open.png");
      this.colors = "#ffffff";
    },
    out() {
      this.open = require("../image/open2.png");
      this.colors = "#cccccc";
    },
    over1() {
      this.personnel = require("../image/open.png");
      this.color1 = "#ffffff";
    },
    out1() {
      this.personnel = require("../image/open2.png");
      this.color1 = "#cccccc";
    },
    showSecede() {
      this.$confirm("确定要退出登录吗？", "退出确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$router.push({ path: "/logion" });
      });
    }
  }
};
</script>

